body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(rgb(66, 66, 238)), to(rgb(128, 219, 238)));
  background: -webkit-linear-gradient(left, rgb(66, 66, 238), rgb(128, 219, 238));
  background: -o-linear-gradient(left, rgb(66, 66, 238), rgb(128, 219, 238));
  background: linear-gradient(to right, rgb(66, 66, 238), rgb(128, 219, 238));
}



